*{font-family:'Roboto', sans-serif}body{background:#1f232a !important;color:#fff !important}.headerTitle{background:#333;color:#fff;padding:10px 0;margin-bottom:1rem}.headerTitle h4{font-weight:300;font-size:20px;text-transform:uppercase;margin:0px;color:#949494}.headerTitle h4 span{color:#ee8c02;font-weight:600}.searchInput{position:relative;padding:0px !important}.searchInput:after{content:"\f002";font-family:'Font Awesome 5 pro';font-weight:900;display:inline-block;width:20px;height:20px;line-height:20px;text-align:center;position:absolute;left:10px;top:7px;z-index:9}.searchInput .searchControl{width:100%;height:100%;border:0px;outline:0 none;padding-left:40px}.cusCard{display:flex;flex-direction:row;flex-flow:wrap;justify-content:baseline;margin-left:-5px;margin-right:-5px}.cusCard .cardContent{border-radius:10px;overflow:hidden;flex:1 0 20%;max-width:25%;background:#333;box-sizing:border-box;margin:0 .5rem 1rem .5rem}.cusCard .cardContent .cardImg{position:relative;overflow:hidden;background:url(https://i.stack.imgur.com/h6viz.gif) no-repeat center 0%;min-height:170px;background-size:57%}.cusCard .cardContent .cardImg img{display:block;width:100%}.cusCard .cardContent .imgDown{position:absolute;bottom:0px;left:0px;padding:10px;background:rgba(0,0,0,0.57);color:#fff;width:100%}.cusCard .cardContent .imgDown ul{padding:0px;margin:0px;list-style:none;font-size:0.8rem}.cusCard .cardContent .imgDown ul li{padding:0}.cusCard .cardContent .imgDown ul li h5{font-weight:400}.cusCardBody{padding:.5rem;overflow:hidden;padding-bottom:1rem}.cusCardBody ul{padding:0px;margin:0px;list-style:none;font-size:0.8rem}.cusCardBody ul li{display:flex;flex-direction:row;justify-content:space-between;color:#fff;border-bottom:1px solid #3c3c3c;padding:8px 0}.cusCardBody ul li:last-child{border-bottom:0px}.cusCardBody ul li span:nth-child(1){color:#949494;text-transform:uppercase}.cusCardBody ul li span:nth-child(2){display:flex;text-align:right;color:#ee8c02}footer{margin-top:30px;padding:10px 0;width:100%;background:rgba(255,255,255,0.1);border-top:1px solid rgba(0,0,0,0.03)}footer a{color:#fff !important}footer .ecart-footer{display:flex;flex-direction:row;justify-content:space-between;font-size:13px}@media (min-width: 767px) and (max-width: 991px){.cusCard .cardContent{flex:1 0 25%;max-width:31%}}@media (max-width: 767px){.mobRow{margin-left:-10px;margin-right:-10px}.cusCard{margin-left:-3px;margin-right:-3px}.cusCard .cardContent{flex:1 0 40%;max-width:51%;margin:0 .2rem 1rem .2rem;border-radius:3px}.cusCard .cardContent .cardImg{min-height:150px}.cusCard .imgDown{padding:5px 3px !important}.cusCard .imgDown ul li h5{font-size:.9rem;font-weight:500 !important;margin-bottom:.1rem}.cusCard .imgDown ul li span{font-size:.7rem}.cusCard .cusCardBody ul li{flex-direction:column !important;padding:4px 0}.cusCard .cusCardBody ul li span{display:block;width:100%;text-align:left !important}}

